body {
    background-color: whitesmoke;
}

.colorBox {
    background-color: mistyrose;
    height: 300px;
    width: 300px;
    margin: 0 auto;
    border: 1px solid black;
}

.content {
    text-align: center;
}

.input-text {
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
}

/* Tablet Landscape */
@media screen and (max-width: 1060px) {

    .content {
        width:67%; 
        margin: 0 auto;
    }
}

/* Tabled Portrait */
@media screen and (max-width: 768px) {

    .content {
        width:100%;
        margin: 0 auto;
    }
}
